import React from 'react';
import Button from '@material-ui/core/Button';

const MyButton = (props) => {
    return (
        <Button 
            href= {props.link}
            target="_blank"
            variant="contained" 
            size='small'
            style={{
                background: props.bck,
                color: props.color,
                fontWeight: "bold"
            }}
        >
            {props.text}
        </Button>
    );
};

export default MyButton;